import React, { Component } from 'react'

import Counter from './Counter';
import Totalcomponent from './Totalcomponent';

export default class App extends Component {

  constructor(){
    super();
    this.state = {
      id1:0,
      id2:0,
      id3:0,
    };
    
  };
  
  

   handleIncrement =(id)=> {
    console.log("Counter function");
    if(id === 1 ){
      this.setState((pv) => ({
        id1: pv.id1 + 1
      }))

    }else if(id === 2){
      this.setState((pv) => ({
        id2: pv.id2 + 1
      }))

    }else if(id === 3){
      this.setState((pv) => ({
        id3: pv.id3 + 1
      }))
    }
   
  } 
   handleDecrement =(id)=> {
    console.log("Decrement function");
    console.log(this,id)
    if(id === 1 ){
      this.setState((pv) => ({
        id1: pv.id1 - 1
      }))

    }else if(id === 2){
      this.setState((pv) => ({
        id2: pv.id2 - 1
      }))

    }else if(id === 3){
      this.setState((pv) => ({
        id3: pv.id3 - 1
      }))
    }
   
  } 

  render() {

    const data = [
      {id:1,value:this.state.id1},
      {id:2,value:this.state.id2},
      {id:3,value:this.state.id3}
    ];

    const total = data[0].value + data[1].value + data[2].value;

    return (
      <div>

      <div className="bg-info text-center p-3">
            <h1>Counter App By Nitest</h1>            
      </div>

      <div className="container mt-4">
        <div className="row">
          { data.map((it)=> 
            <div className="col-4 text-center " key={it.id}>
                  <div className="bg-light">
                  <h4 className="p-4">Value Of Id{it.id}</h4>
                  <h1 className="pb-3 text-info">{it.value}</h1>
                  </div>
            </div>
          )}                      
          </div>
      </div>

      <div className="container mt-3 mb-3">
        <div className="row">
          <div className="col-4"></div>
          <div className="col-4"><Totalcomponent total={total} /></div>
        </div>
        
      </div>

        <h2 className="text-center mt-3">Componet Controller </h2>
        <div className="container">
          <div className="row">
          {data.map((it)=>
            <div className="col-4">
              <div className="p-3 bg-info">
              <Counter key={it.id} value={it.value} handle={it.id} incrementAction={this.handleIncrement}decrementAction={this.handleDecrement}/>
              </div>
            </div>
          )}
          </div>
        
          
        
        </div>

        


       

        
      </div>
    )
  }
}
