import React, { Component } from 'react'

export default class Counter extends Component { 

    render() {
  
      const { value, incrementAction, handle,decrementAction } = this.props;
      const incrementHandler =()=>{
        incrementAction(handle)
      }
      const decrementtHandler =()=>{
        decrementAction(handle)
      }
  
      return (
  
        <div className="counter">
  
          
  
            <div className="btn "><b>{value}</b></div> 
          <div className="counter-controls"> 
            <button className="button btn btn-light" onClick={decrementtHandler}>-</button>  
            <button className="button  btn btn-light" onClick={incrementHandler}>+</button>  
          </div>
  
        </div>
  
      );
  
    }
  
  }